/* Global Section */

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 18px;
}

p {
  font-size: 15px;
  font-family: sans-serif;
}

h2 {
  color: rgb(19, 19, 82);
}

h3 {
  padding-bottom: 10px;
  color: rgb(59, 59, 107);
}

.container-main {
  padding: 12px;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 75px 1fr;
  gap: 10px;
  height: 900px;
}

/* Header Section */
.header-main {
  grid-column: 1/-1;
  background-color: #f8f8f8;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.header-container {
  gap: 20px;
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.header-logo {
  flex: 0;
  height: 100%;
  align-self: stretch;
}

.header-context {
  height: 100%;
  flex: 1;
  display: flex;
  justify-content: center;
}

.header-text {
  align-self: center;
}

/* Middle Section */
.section-middle {
  grid-column: 1/-1;
  display: grid;
  gap: 20px;
  grid-template-columns: 1fr;
  grid-template-rows: 300px 1fr 1fr;
  justify-content: space-between;
}

/* Middle Section */
/* Drop Zone */

.section-middle-browse {
}

.section-middle-dropzone {
  justify-items: center;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  display: grid;
  gap: 10px;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

.section-middle-browse-unitary {
  text-align: center;
}

.section-middle-browse-unitary p {
  padding: 10px;
}

.dropzone {
  border: 2px solid rgb(255, 255, 255);
  background-color: #f4f4f4;
  background-image: linear-gradient(
      45deg,
      #dfdddd 25%,
      transparent 25%,
      transparent 75%,
      #dfdddd 75%
    ),
    linear-gradient(
      45deg,
      #dfdddd 25%,
      transparent 25%,
      transparent 75%,
      #dfdddd 75%
    );
  background-size: 25px 25px;
  background-position: 0 0, 12px 12px;
  width: 650px;
  padding: 0px;
  height: auto;
  display: grid;
  gap: 10px;
  grid-template-columns: 1fr 75px 1fr;
  grid-template-rows: 1fr 1fr;
  text-align: center;
  font-weight: bold;
  border-radius: 10px;
}

/* #drop_zone:hover {
  background-color: #c9cbda;
} */

.upload-img {
  width: 74px;
  justify-self: stretch;
  grid-column: 2/3;
  grid-row: 1/2;
}

#myProgress {
  width: 100%;
  height: auto;
}

#myBar {
  width: 0%;
  height: 19px;
  background-color: #1176be;
}

.uploaded {
  font-size: 15px;
  font-weight: normal;
  text-align: center;
  /* padding: 0 40.81%; */
  color: rgb(255, 255, 255);
}

.section-middle-dropzone-output {
  display: grid;
  grid-template-columns: 2em 1fr;
  grid-template-rows: 40px 1fr;
  justify-self: stretch;
  align-self: stretch;
  padding: 0px;
}

.query-input-title {
  padding: 5px;
  grid-column: 2/2;
  grid-row: 1/1;
}

.query-input-preview {
  text-align: left;
  color: black;
  border-radius: 10px;
  grid-column: 2/3;
  grid-row: 2/2;
}

.preview {
  background-color: #2980b9;
  width: auto;
  color: white;
  font-size: 12px;
}

.highlight {
  border: 2px solid #2a1650;
  background-color: #c0d5e4;
}

.drag-drop-label {
  grid-column: 1/1;
  grid-row: 2/2;
  align-self: center;
  justify-self: end;
}

.drag-drop-alternative {
  grid-column: 2/2;
  grid-row: 2/2;
  align-self: center;
  justify-self: center;
  font-size: 30px;
  font-weight: bold;
  color: grey;
}
.section-middle-getfilebutton {
  width: 120px;
  height: 30px;
  grid-column: 3/3;
  grid-row: 2/2;
  align-self: center;
  justify-self: start;
}

/* Middle Section */
/* Query Options */

.section-middle-queryoptions {
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

/* Lower Section */
/* Result Table */
.section-middle-table {
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px,
    rgba(60, 64, 67, 0.15) 0px 2px 6px 2px;
}

/* Spinner */
.loader {
  grid-row: 1/2;
  grid-column: 1/2;
  font-size: 10px;
  margin-left: 20%;
  margin-top: 5px;
  text-indent: -9999em;
  overflow: hidden;
  width: 2.5em;
  height: 2.5em;
  border-radius: 50%;
  background: #1a5fb4;
  background: -moz-linear-gradient(left, #1a5fb4 10%, rgba(26, 95, 180, 0) 42%);
  background: -webkit-linear-gradient(
    left,
    #1a5fb4 10%,
    rgba(26, 95, 180, 0) 42%
  );
  background: -o-linear-gradient(left, #1a5fb4 10%, rgba(26, 95, 180, 0) 42%);
  background: -ms-linear-gradient(left, #1a5fb4 10%, rgba(26, 95, 180, 0) 42%);
  background: linear-gradient(to right, #1a5fb4 10%, rgba(26, 95, 180, 0) 42%);
  position: relative;
  -webkit-animation: load3 1.4s infinite linear;
  animation: load3 1.4s infinite linear;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
}
.loader:before {
  width: 50%;
  height: 50%;
  background: #1a5fb4;
  border-radius: 100% 0 0 0;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
}
.loader:after {
  background: #ffffff;
  width: 75%;
  height: 75%;
  border-radius: 50%;
  content: "";
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
@-webkit-keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load3 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
